
import {Options, Vue} from 'vue-class-component';

@Options({
    data(){
        return{
            newMission:"",
        }
    },
    props:{
        status: Boolean
    },
    emits: {
        add: String
    }
})

export default class AddMissionBar extends Vue {
    add!:string
    newMission!:string
    status!: boolean
    
    emitNewMission(){
        if(this.newMission !== undefined && this.newMission !== ''){
            this.$emit('add', this.newMission)
            this.newMission = ""
        }
    }
}
